import React, { useState, useEffect } from 'react';
import Letter from '../../../common/Letter'
import { Tooltip, Space, Checkbox } from 'antd'
import { QuestionCircleTwoTone, CheckCircleTwoTone, ExclamationCircleTwoTone } from '@ant-design/icons'
import KgInput from '../../../common/KgInput'
import { isVowelLetter, isVoicedConsonant } from '../../../../service/utils'

interface QuestionAffixPracticeItemProps {
  word: string
}

const QuestionAffixPracticeItem = (props: QuestionAffixPracticeItemProps): JSX.Element => {
  const { word } = props
  const valueState = useState('')
  const wordArray = word.split('')
  const [answerState, setAnswerState] = useState<boolean | null>(null) //  : null - , true - , false - 
  const [showHint, setShowHint] = useState<boolean>(false)

  const getLastVowelLetter = (): string => {
    let result: string = '-'
    for (let i = wordArray.length - 1; i >= 0; i--) {
      if (isVowelLetter(wordArray[i])) {
        result = wordArray[i]
        break
      }
    }
    return result
  }
  //    
  const lastVowelLetter: string = getLastVowelLetter()
  //   
  const lastLetter = wordArray[wordArray.length - 1]

  let rightAffix = '-'
  if (lastVowelLetter === '' || lastVowelLetter === '' || lastVowelLetter === '') {
    if (isVowelLetter(lastLetter) || isVoicedConsonant(lastLetter)) {
      rightAffix = ''
    } else {
      rightAffix = ''
    }
  } else if (lastVowelLetter === '' || lastVowelLetter === '' || lastVowelLetter === '') {
    if (isVowelLetter(lastLetter) || isVoicedConsonant(lastLetter)) {
      rightAffix = ''
    } else {
      rightAffix = ''
    }
  } else if (lastVowelLetter === '' || lastVowelLetter === '') {
    if (isVowelLetter(lastLetter) || isVoicedConsonant(lastLetter)) {
      rightAffix = ''
    } else {
      rightAffix = ''
    }
  } else if (lastVowelLetter === '' || lastVowelLetter === '' || lastVowelLetter === '' || lastVowelLetter === '') {
    if (isVowelLetter(lastLetter) || isVoicedConsonant(lastLetter)) {
      rightAffix = ''
    } else {
      rightAffix = ''
    }
  }

  useEffect(() => {
    if (valueState[0] !== '') {
      setAnswerState(valueState[0].toLowerCase() === rightAffix.toLowerCase())
    } else {
      setAnswerState(null)
    }
  }, [valueState[0]])

  const AnswerStateIcon = (): JSX.Element => {
    if (answerState === null) {
      return (<Tooltip title={' '}><QuestionCircleTwoTone twoToneColor={'orange'}/></Tooltip>)
    }

    if (answerState) {
      return (<Tooltip title={' '}><CheckCircleTwoTone twoToneColor="#52c41a"/></Tooltip>)
    }

    return (<Tooltip title={' '}><ExclamationCircleTwoTone twoToneColor={'red'}/></Tooltip>)
  }

  return (
    <Space>
      <div style={{ minWidth: '50px' }}>
        {word}
      </div>
      <KgInput valueState={valueState}
               placeholder={'   '}
               width={'80px'}
      />
      <AnswerStateIcon/>
      <Tooltip title={' '}>
        <Checkbox checked={showHint}
                  onChange={() => {
                    setShowHint(!showHint)
                  }}/>
      </Tooltip>
      {showHint && <>
          <b>{rightAffix}</b>   : <Letter value={lastVowelLetter}/>
            : <Letter value={lastLetter}/>
      </>}
    </Space>
  )
}

export default QuestionAffixPracticeItem
